import {Component, Input} from '@angular/core';

@Component({
  selector: 'ngbd-alert-basic',
  templateUrl: './alert-basic.html'
})
export class NgbdAlertBasic {
  @Input()
  public alerts: Array<any> = [];

  constructor() {
    this.alerts.push({
      id: 5,
      type: 'primary',
      message: 'A simple primary alertcheck it out! ',
    }, {
      id: 6,
      type: 'secondary',
      message: 'A simple secondary alertcheck it out! ',
    }, {
      id: 1,
      type: 'success',
      message: 'A simplen success alertcheck it out! ',
    }, {
      id: 4,
      type: 'danger',
      message: 'A simple danger alertcheck it out! ',
    }, {
      id: 3,
      type: 'warning',
      message: 'A simple warning alertcheck it out! ',
    }, {
      id: 2,
      type: 'info',
      message: 'A simplen info alertcheck it out! ',
    }, {
      id: 7,
      type: 'light',
      message: 'A simple light alertcheck it out! ',
    }, {
      id: 8,
      type: 'dark',
      message: 'A simple dark alertcheck it out! ',
    });
  }
}
